header{
  background-color: black;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: white;
  margin-bottom: 10px;
}

.header-title{
  margin-left: 10px;
  font-family: Monospace;
  font-size: 20px;
}

header nav a{
  text-decoration: none;
  color: white;
  margin: 10px;
  padding: 5px;
}

header nav a:hover{
  color: aqua;
  border: 1px solid gray;
  background-color: gray;
  border-radius: 5px;
}
