.hacker-info{
  padding: 10px;
  display: grid;
  text-align: center;
  grid-template-columns: repeat(3, 1fr);
}

.hacker-info img{
  clip-path: circle();
  margin: 5px;
  width: 200px;
  height: 200px;
  
}

.hackers{
  padding: 5px;
  margin: 5px;
  background-color: gainsboro;
  border: 2px solid gray;
  border-radius: 10px;
}

.aline{
  margin-left: 15px;
}

.btn{
  text-align: center;
}
.addtocart{
  width: 150px;
  height: 30px;
  background-color: goldenrod;
  border-radius: 5px;
  border: 1px solid goldenrod;
}